@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*******************************NAVBAR********************************/

.nav {
z-index: 2;
}

.logo {
  width: 8.45rem;
  height: 8rem;
}

/****************************MENU LINKS*****************************/
nav a.nav-link {
  font-weight: 100;
  letter-spacing: 0.6px;
  cursor: pointer;
  
}

nav ul li {
  text-transform: uppercase;
  letter-spacing: 0.6px;
  z-index: 2;

}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

:root {
  --primary-white: #fff;
  --primary-blue:#89CFF0;
  --primary-dark:rgba(255,255,255,0);
  --primary-black: #000;
  --primary-pink: #cc8899;
}
.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255,255,255,0);
  border-top: 0.1875rem solid var(--primary-dark);

}

.navbar-brand {
  max-width: 6.45rem;
  height: 8rem !important;
  padding: 0 !important ;
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #89CFF0;
  border-top: 0.1875rem solid var(--primary-blue) ;
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #89CFF0;
  border-top: 0.1875rem solid var(--primary-blue) ;
}

/***************************HEADER*********************************/

.header-wrapper {
  position: relative;
  background: url(/portfolio-project/static/media/laptop.84776ed7.jpg) no-repeat;
  background-blend-mode: soft-light;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condesed",sans-serif;
  z-index: 1;

}

.main-info h1 {
  color: #000;
  color: var(--primary-black);
  text-transform: uppercase;
}

@media(max-width:768px) {
  .main-info {
    font-size: 1rem !important;
  }
}


/********************TYPED TEXT************************/

.typed-text {
  font-size: 2rem;
  color: #000;
  color: var(--primary-black);
  text-transform: uppercase;
  background: whitesmoke;

}

@media(max-width:768px) {
  .typed-text {
    font-size: 1rem;
  }
}

/*********************PARTICLES JS************************/

.tsparticles-canvas-el {
position: absolute;
height: 100vh;
width: 100vw;
z-index: 1;
}

@media(max-width:768px) {
  .particles-canvas {
    display: none;
  }
}

/*********************ABOUT ME************************/

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #89CFF0;
  border: 1px solid var(--primary-blue);

}

.aboutme_heading {
  color: #000;
  color: var(--primary-black);
  text-transform: uppercase;
}

@media(max-width:768px) {
  .profile-img {
    width: 10rem;
  }
}

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}


/*********************SKILLS************************/

.skill {
  background-color: #000;
  background-color: var(--primary-black);
  height: 40%;
}


.icon {
  width: auto;
  height: 200px;
  margin: 10px;
  padding: 60px;
  z-index: 2;
}

.skill-header {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  color: #000;
  color: var(--primary-black);
  margin-left: 40%;
  z-index: 3;
}

/*********************EXPERIENCE************************/

.experience-header {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  color: #000;
  color: var(--primary-black);
}

.experience-wrapper {
  position: relative;
  padding: 3.15rem 0;
  overflow: hidden;
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top:0;
  left: 50%;
  margin-left:-0.06rem;
  width: 0.125rem;
  height: 100%;
  background-color: #89CFF0;
  background-color: var(--primary-blue);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width:1rem;
  height:1rem;
  border-radius: 50%;
  border: 0.3125rem solid #cc8899;
  border: 0.3125rem solid var(--primary-pink);
  background-color: #000;
  background-color: var(--primary-black);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #000;
  color: var(--primary-black);
  direction: ltr;
}

.timeline-content h3{
  margin-top: 0.3125rem;
  color: #89CFF0;
  color: var(--primary-blue);
  text-shadow: black;
  font-weight: 900;
}

.timeline-content h6 {
  text-transform: uppercase;
  font-weight: 600;
}


/*********************EDUCATION************************/


.education-header {
  text-transform: uppercase;
  justify-content: center;
  color: #000;
  color: var(--primary-black);
  margin-left: 40%;
}

.education-container {
  background-color: #89CFF0;
  background-color: var(--primary-blue);
  color: #000;
  color: var(--primary-black);
}

.year {
  float: right;
  margin-top: -65px;
  margin-right: 10%;
}

.edu-content {
  padding: 5px;
  margin: 30px;
}


/*********************CONTACT ME************************/

.contacts {
 background-color: #000;
 background-color: var(--primary-black);
 padding: 3rem 0;
}

.contacts h1 {
  color: #cc8899;
  color: var(--primary-pink);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #89CFF0;
  color: var(--primary-blue);
  font-size: 1.2rem;
}


/*---------------------INPUTS---------------------------------*/

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #cc8899;
  border-bottom: 0.125rem solid var(--primary-pink);
  border-radius: 0;
  color: #89CFF0;
  color: var(--primary-blue);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background-color: transparent;
  color: #89CFF0;
  color: var(--primary-blue);
  border-bottom: 0.25rem solid #89CFF0;
  border-bottom: 0.25rem solid var(--primary-blue);
}

.contacts button {
  margin: 1rem 0 0 0;
  width: 100%;
  border: 1px solid #cc8899;
  border: 1px solid var(--primary-pink);
  background-color: transparent;
  color: #cc8899;
  color: var(--primary-pink);
}

.contacts button:hover {
  background-color: #89CFF0;
  background-color: var(--primary-blue);
}

.contacts textarea {
  height: 12.4rem !important;
}


/*---------------------MEDIA------------------------------*/

@media(max-width:768px) {
  .contacts {
overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding:0 0.4rem;
  }
}

.error-message {
  color: #cc8899;
  color: var(--primary-pink);
}


/*********************FOOTER************************/

.footer{
  background-color: grey;
  color: #000;
  color: var(--primary-black);
  padding:3rem;
}

.footer a {
text-decoration:none;
color: #000;
color: var(--primary-black);
}

.footer a:hover {
  text-decoration:none;
  color: #89CFF0;
  color: var(--primary-blue);
  cursor: pointer;
}

.footer p {
  margin-bottom: 1rem;
}
